
import React, { Component } from "react";
import "../../../assets/style.css";
import nextIcon from "../../../assets/images/next-icon.png";
import $ from "jquery";
import jQuery from "jquery";
import MediaQuery from "react-responsive";

class ImageMagnifier extends Component {
  componentDidMount() {
    /**
     * by MonsterDuang
     */
    (function ($) {
      /**
       * 1, 
       * 2,  href 
       */
      $.fn.zoomImage = function (paras) {
        /**
         * 
         */
        var defaultParas = {
          layerW: "100%", // 
          layerH: 100, // 
          layerOpacity: 0.2, // 
          layerBgc: "#000", // 
          showPanelW: 500, // 
          showPanelH: 500, // 
          marginL: 5, // 
          marginT: 0, // 
        };

        paras = $.extend({}, defaultParas, paras);

        $(this).each(function () {
          var self = $(this).css({ position: "relative" });
          var selfOffset = self.offset();
          var imageW = self.width(); // 
          var imageH = self.height();

          self.find("img").css({
            width: "100%",
            height: "100%",
          });

          // 
          var wTimes = paras.showPanelW / paras.layerW;
          // 
          var hTimes = paras.showPanelH / paras.layerH;

          // 
          var img = $("<img>")
            .attr("src", self.attr("href"))
            .css({
              position: "absolute",
              left: "0",
              top: "0",
              width: imageW * wTimes,
              height: imageH * hTimes,
            })
            .attr("id", "big-img");

          // 
          var layer = $("<div>").css({
            display: "none",
            position: "absolute",
            left: "0",
            top: "0",
            backgroundColor: paras.layerBgc,
            width: paras.layerW,
            height: paras.layerH,
            opacity: paras.layerOpacity,
            border: "1px solid #ccc",
            cursor: "crosshair",
          });

          // 
          var showPanel = $("<div>")
            .css({
              display: "none",
              position: "absolute",
              overflow: "hidden",
              zIndex: "1",
              left: imageW + paras.marginL,
              top: paras.marginT,
              width: "750px",
            //   width: paras.showPanelW,
              height: "400px",
              border: "1px solid black",
            //   height: paras.showPanelH,
            })
            .append(img);

          self.append(layer).append(showPanel);

          self
            .on("mousemove", function (e) {
              // 
              var x = e.pageX - selfOffset.left;
              var y = e.pageY - selfOffset.top;

              if (x <= paras.layerW / 2) {
                x = 0;
              } else if (x >= imageW - paras.layerW / 2) {
                x = imageW - paras.layerW;
              } else {
                x = x - paras.layerW / 2;
              }

              if (y < paras.layerH / 2) {
                y = 0;
              } else if (y >= imageH - paras.layerH / 2) {
                y = imageH - paras.layerH;
              } else {
                y = y - paras.layerH / 2;
              }

              layer.css({
                left: x,
                top: y,
              });

              img.css({
                left: -x * wTimes,
                top: -y * hTimes,
              });
            })
            .on("mouseenter", function () {
              layer.show();
              showPanel.show();
            })
            .on("mouseleave", function () {
              layer.hide();
              showPanel.hide();
            });
        });
      };
    })(jQuery);

    $(".show").zoomImage();

    $(".show-small-img:first-of-type").css({
      border: "solid 1px #951b25",
      padding: "2px",
    });
    $(".show-small-img:first-of-type")
      .attr("alt", "now")
      .siblings()
      .removeAttr("alt");
    $(".show-small-img").on("click", function () {
      $("#show-img").attr("src", $(this).attr("src"));
      $("#big-img").attr("src", $(this).attr("src"));
      $(this).attr("alt", "now").siblings().removeAttr("alt");
      $(this)
        .css({ border: "solid 1px #951b25", padding: "2px" })
        .siblings()
        .css({ border: "none", padding: "0" });
      if ($("#small-img-roll").children().length > 4) {
        if (
          $(this).index() >= 3 &&
          $(this).index() < $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css("left", -($(this).index() - 2) * 76 + "px");
        } else if (
          $(this).index() ==
          $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css(
            "left",
            -($("#small-img-roll").children().length - 4) * 76 + "px"
          );
        } else {
          $("#small-img-roll").css("left", "0");
        }
      }
    });

    //Enable the next button

    $("#next-img").on("click", function () {
      $("#show-img").attr(
        "src",
        $(".show-small-img[alt='now']").next().attr("src")
      );
      $("#big-img").attr(
        "src",
        $(".show-small-img[alt='now']").next().attr("src")
      );
      $(".show-small-img[alt='now']")
        .next()
        .css({ border: "solid 1px #951b25", padding: "2px" })
        .siblings()
        .css({ border: "none", padding: "0" });
      $(".show-small-img[alt='now']")
        .next()
        .attr("alt", "now")
        .siblings()
        .removeAttr("alt");
      if ($("#small-img-roll").children().length > 4) {
        if (
          $(".show-small-img[alt='now']").index() >= 3 &&
          $(".show-small-img[alt='now']").index() <
            $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css(
            "left",
            -($(".show-small-img[alt='now']").index() - 2) * 76 + "px"
          );
        } else if (
          $(".show-small-img[alt='now']").index() ==
          $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css(
            "left",
            -($("#small-img-roll").children().length - 4) * 76 + "px"
          );
        } else {
          $("#small-img-roll").css("left", "0");
        }
      }
    });

    //Enable the previous button

    $("#prev-img").on("click", function () {
      $("#show-img").attr(
        "src",
        $(".show-small-img[alt='now']").prev().attr("src")
      );
      $("#big-img").attr(
        "src",
        $(".show-small-img[alt='now']").prev().attr("src")
      );
      $(".show-small-img[alt='now']")
        .prev()
        .css({ border: "solid 1px #951b25", padding: "2px" })
        .siblings()
        .css({ border: "none", padding: "0" });
      $(".show-small-img[alt='now']")
        .prev()
        .attr("alt", "now")
        .siblings()
        .removeAttr("alt");
      if ($("#small-img-roll").children().length > 4) {
        if (
          $(".show-small-img[alt='now']").index() >= 3 &&
          $(".show-small-img[alt='now']").index() <
            $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css(
            "left",
            -($(".show-small-img[alt='now']").index() - 2) * 76 + "px"
          );
        } else if (
          $(".show-small-img[alt='now']").index() ==
          $("#small-img-roll").children().length - 1
        ) {
          $("#small-img-roll").css(
            "left",
            -($("#small-img-roll").children().length - 4) * 76 + "px"
          );
        } else {
          $("#small-img-roll").css("left", "0");
        }
      }
    });
  }

  render() {
    return (
      <>
      <MediaQuery minWidth={1224}>
        <div
          class="show"
          href={
            process.env.REACT_APP_BASE_IMAGE_PATH + this.props.images[0].path
          }
        >
          <img
            src={
              process.env.REACT_APP_BASE_IMAGE_PATH + this.props.images[0].path
            }
            id="show-img"
          />
        </div>
        <div class="small-img">
          <img src={nextIcon} class="icon-left" alt="" id="prev-img" />
          <div class="small-container">
            <div id="small-img-roll">
              {this.props.images.map((image) => (
                <img
                  src={process.env.REACT_APP_BASE_IMAGE_PATH + image.path}
                  class="show-small-img"
                  alt=""
                />
              ))}
            </div>
          </div>
          <img src={nextIcon} class="icon-right" alt="" id="next-img" />
        </div>
        </MediaQuery>
        <MediaQuery maxWidth={1224}>
        <div
          href={
            process.env.REACT_APP_BASE_IMAGE_PATH + this.props.images[0].path
          }
        >
          <img
            src={
              process.env.REACT_APP_BASE_IMAGE_PATH + this.props.images[0].path
            }
            id="show-img"
          />
        </div>

        <div class="small-img">
          <img src={nextIcon} class="icon-left" alt="" id="prev-img" />
          <div class="small-container">
            <div id="small-img-roll">
              {this.props.images.map((image) => (
                <img
                  src={process.env.REACT_APP_BASE_IMAGE_PATH + image.path}
                  class="show-small-img"
                  alt=""
                />
              ))}
            </div>
          </div>
          <img src={nextIcon} class="icon-right" alt="" id="next-img" />
        </div>
        </MediaQuery>
      </>
    );
  }
}
export default ImageMagnifier;




